.image{
  height: 80%;
  width: 100%;
  filter: blur(4px);
  -webkit-filter: blur(4px);
}

.bg-text {
  background-color: rgb(0,0,0); /* Fallback color */
  background-color: rgba(0, 0, 0, 0.705); 
  color: white;
  font-weight: bold;
  border: 3px solid #f1f1f1;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 2;
  width: 80%;
  height: 60%;
  padding: 20px;
  text-align: center;
}
